/*
LESS variables are information about icon's compiled state, stored under its original file name

.icon-home {
  width: @icon-home-width;
}

The large array-like variables contain all information about a single icon
@icon-home: x y offset_x offset_y width height total_width total_height image_path name;

At the bottom of this section, we provide information about the spritesheet itself
@spritesheet: width height image @spritesheet-sprites;
*/

/*
The provided classes are intended to be used with the array-like variables

.icon-home {
  .sprite-width(@icon-home);
}
.icon-email {
  .sprite(@icon-email);
}

Example usage in HTML:

`display: block` sprite:
<div class="icon-home"></div>

To change `display` (e.g. `display: inline-block;`), we suggest using a common CSS class:

// CSS
.icon {
  display: inline-block;
}

// HTML
<i class="icon icon-home"></i>
*/

/*
The `.sprites` mixin generates identical output to the CSS template
  but can be overridden inside of LESS

This must be run when you have at least 2 sprites.
  If run with a single sprite, then there will be reference errors.

.sprites(@spritesheet-sprites);
*/

@font-face {
  font-family: "Lato";
  src: url("../fonts/Lato-Regular.eot");
  src: url("../fonts/Lato-Regular.eot?#iefix") format("embedded-opentype"), url("../fonts/Lato-Regular.woff") format("woff"), url("../fonts/Lato-Regular.woff2") format("woff2"), url("../fonts/Lato-Regular.ttf") format("truetype");
  font-style: normal;
  font-weight: 400;
}

@font-face {
  font-family: "LatoSemibold";
  src: url("../fonts/Lato-Semibold.eot");
  src: url("../fonts/Lato-Semibold.eot?#iefix") format("embedded-opentype"), url("../fonts/Lato-Semibold.woff") format("woff"), url("../fonts/Lato-Semibold.woff2") format("woff2"), url("../fonts/Lato-Semibold.ttf") format("truetype");
  font-style: normal;
  font-weight: 600;
}

body,
div,
dl,
dt,
dd,
ul,
li,
h1,
h2,
h3,
h4,
h5,
h6,
pre,
code,
form,
fieldset,
input,
textarea,
p,
blockquote,
th,
td {
  margin: 0;
  padding: 0;
}

table {
  border-collapse: collapse;
  border-spacing: 0;
}

fieldset,
img,
abbr {
  border: 0;
}

address,
caption,
cite,
code,
dfn,
em,
strong,
th,
var {
  font-style: normal;
  font-weight: normal;
}

ul li {
  list-style: none;
}

caption,
th {
  text-align: left;
}

h1,
h2,
h3,
h4,
h5,
h6 {
  font-size: 100%;
  font-weight: normal;
}

sup {
  vertical-align: text-top;
}

sub {
  vertical-align: text-bottom;
}

input,
textarea,
select {
  font-family: inherit;
  font-size: inherit;
  font-weight: inherit;
}

legend {
  color: #000;
}

article,
aside,
details,
figcaption,
figure,
footer,
header,
hgroup,
menu,
nav,
section,
main {
  display: block;
}

img {
  max-width: 100%;
  height: auto;
}

a {
  text-decoration: none;
}

nav li {
  list-style: none;
}

.wrapper {
  max-width: 1280px;
  padding-left: 30px;
  padding-right: 30px;
  margin: 0 auto;
}

h1 {
  font-size: 3.75rem;
  font-family: 'LatoSemibold';
}

h2 {
  font-size: 1.875rem;
  font-family: 'LatoSemibold';
}

h3 {
  font-size: 1.875rem;
}

h4 {
  font-size: 1.5rem;
}

h5 {
  font-size: 1.25rem;
}

h6 {
  font-size: 1.125rem;
}

p {
  font-size: 16px;
}

* {
  box-sizing: border-box;
}

body {
  color: #000;
  max-width: 1920px;
  font-family: 'Lato', Arial, sans-serif;
  font-weight: 400;
  font: 16px/2rem 'Lato', Arial, sans-serif;
}

body img {
  max-width: 100%;
  height: auto;
}

/* Atomic */

.d-n {
  display: none !important;
}

.text-white {
  color: #fff;
}

.text-color {
  color: #000;
}

.text-red {
  color: #f61729;
}

.ta-c {
  text-align: center;
}

.cur-p {
  cursor: pointer;
}

.d-b {
  display: block;
}

.bg-white {
  background-color: #fff;
}

.bg-red {
  background-color: #f61729;
}

.tt-u {
  text-transform: uppercase;
}

/* Styles */

@keyframes moving {
  0% {
    transform: translateY(0px);
  }

  25% {
    transform: translateY(-5px);
  }

  50% {
    transform: translateY(5px);
  }

  100% {
    transform: translateY(0px);
  }
}

@keyframes head-appear {
  from {
    opacity: 0;
  }

  to {
    opacity: 0.8;
  }
}

/* Общие стили для страницы */

.icon {
  width: 15px;
  height: 15px;
  margin: 0 5px;
  position: relative;
  top: 2px;
  z-index: 1;
}

.icon__play {
  display: inline-block;
  width: 15px;
  height: 15px;
  position: relative;
  top: 2px;
}

.icon__hamburger {
  width: 25px;
  height: 25px;
  margin: 10px;
  transition: transform 0.5s, opacity 0.5s;
  cursor: pointer;
}

.icon__hamburger:hover {
  transform: scale(1.1);
}

.icon__hamburger.clicked {
  display: block !important;
  opacity: 1;
  position: fixed;
  z-index: 5000;
  top: 15px;
  right: 15px;
}

.btn {
  width: 215px;
  background-color: transparent;
  border: 2px solid #f61729;
  border-radius: 5px;
  padding: 15px 5px;
  cursor: pointer;
}

.h__lines {
  position: relative;
  z-index: 1;
}

.h__lines:before,
.h__lines:after {
  content: '';
  display: block;
  position: absolute;
  border-top: 1px solid #f61729;
  width: 20px;
  top: 50%;
  z-index: 2;
}

.h__lines:before {
  left: -35px;
}

.h__lines:after {
  right: -35px;
}

/* Header */

header.header {
  background: #fff url(../img/first_screen_bg.jpg) no-repeat top;
  background-size: cover;
}

.scroll-header.header__nav {
  position: fixed;
  padding: 0px 15px;
  animation: head-appear 0.5s;
  width: 100%;
  top: 0;
  left: 0;
  background-color: rgba(119, 119, 119, 0.6);
  z-index: 1000;
}

.scroll-header.header__nav .logotype {
  margin-left: 0;
}

.scroll-header.header__nav .logotype__svg {
  width: 130px;
}

.scroll-header.header__nav .logotype__sign {
  font-size: 12px;
  left: 0;
  bottom: -15px;
}

.scroll-header.header__nav img.logo_head {
  width: 70px;
  height: auto;
}

.header__nav {
  padding: 16px 0;
  display: flex;
  justify-content: space-between;
  align-items: center;
}

.logotype {
  width: 470px;
  -webkit-margin-before: 0;
  -webkit-margin-after: 0;
  -webkit-margin-start: 0;
  -webkit-margin-end: 0;
  min-width: 360px;
  margin-left: 25px;
  position: relative;
  z-index: 1;
}

.logotype__svg {
  width: 50%;
  max-width: 240px;
  min-width: 130px;
}

.logotype__sign {
  font-size: 14px;
  position: absolute;
  z-index: 2;
  bottom: 0px;
  right: 40px;
}

.navblock__menu {
  display: flex;
  justify-content: space-between;
  padding: 5px 0;
}

.navblock__menu.hamburger {
  padding: 10px;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  z-index: 4000;
  background-color: rgba(119, 119, 119, 0.9);
}

.navblock__menu.hamburger li {
  font-size: 18px;
  font-weight: bold;
  margin: 0;
}

.navblock__menu.hamburger li + li {
  margin-top: 30px;
}

.navblock__item + .navblock__item {
  margin-left: 20px;
}

.navblock__link {
  color: #fff;
  padding-bottom: 5px;
  transition: border-bottom-color 0.5s;
  border-bottom: 2px solid transparent;
}

.navblock__link:hover {
  border-bottom-color: #f61729;
}

.first-screen {
  padding: 150px 0;
}

.first-screen__h1 {
  line-height: 3rem;
  margin-bottom: 80px;
}

.first-screen__h1_span {
  font-size: 30px;
}

.first-screen__footer {
  padding: 0 0 55px;
}

.buttons-container {
  text-align: center;
}

.buttons-container__button {
  color: #fff;
  display: inline-block;
  height: 50px;
}

.buttons-container__button_white {
  margin-left: 10px;
  border-color: #fff;
  position: relative;
  top: 5px;
  z-index: 1;
  transition: color 0.5s;
}

.buttons-container__button_white:hover {
  color: #f61729;
}

.buttons-container__button_white:hover .st0 {
  transition: fill 0.5s;
  fill: #f61729;
}

.buttons-container__button_white span {
  position: relative;
  bottom: 1px;
  z-index: 1;
}

.buttons-container__button_red {
  margin-right: 10px;
  z-index: 1;
}

.buttons-container__button_red:hover {
  transition: color 0.5s;
  color: #f61729;
}

.link__down {
  display: block;
  width: 30px;
  height: 20px;
  margin: 5px auto;
  animation: moving 1s cubic-bezier(0.14, 1.02, 0.58, 1.25) infinite paused;
}

.link__down:hover {
  animation-play-state: running;
}

/*
		END Header

		Who we are
*/

.who-we-are {
  padding: 120px 0;
}

.who-we-are .wrapper {
  display: flex;
  justify-content: space-between;
  flex-wrap: wrap;
  align-items: center;
}

.who-we-are__figure {
  width: 580px;
  flex-grow: 1;
}

.who-we-are__textblock {
  width: 400px;
  flex-grow: 1;
  padding: 40px 10px;
  line-height: 1.5rem;
}

.who-we-are__h2 {
  margin-bottom: 20px;
}

.who-we-are__p {
  margin-bottom: 20px;
}

.who-we-are__button:hover {
  transition: background-color 0.5s, color 0.5s;
  background-color: #f61729;
  color: #fff;
}

/*
	END Who we are

	Partners
*/

.partners {
  padding: 10px 0 100px;
}

.partners__h2 {
  margin-bottom: 40px;
}

.partners__logos {
  display: flex;
  flex-wrap: wrap;
  margin-left: -15px;
  margin-right: -15px;
  justify-content: center;
  align-items: center;
}

.partners__logo {
  box-sizing: border-box;
  margin-left: 15px;
  margin-right: 15px;
  word-wrap: break-word;
  width: calc(25% - 30px);
}

.partners__logo_img {
  display: block;
  margin: 10px auto;
}

/*

	END Partners

	Video

*/

.video {
  padding: 10px 0 80px;
}

.video__h2 {
  margin-bottom: 40px;
}

.video__figure {
  width: 83%;
  padding: 0 10px;
  margin: 0 auto;
}

.video__relation {
  padding-top: 56.25%;
  height: 0;
  position: relative;
  z-index: 1;
}

.video__frame {
  position: absolute;
  z-index: 2;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
}

/*

	END Video

	Contacts

*/

.contacts {
  padding: 110px 15px;
  background: #fff url(../img/contacts_screen_bg.jpg) no-repeat top;
  background-size: cover;
}

.contacts__h2 {
  margin-bottom: 80px;
}

.contacts__blocks {
  display: flex;
  flex-wrap: wrap;
  margin-left: -15px;
  margin-right: -15px;
  justify-content: space-between;
  align-items: center;
}

.contacts__block {
  width: calc(41.66666666666667% - 30px);
  margin: 20px 0 30px;
}

.contacts__aside {
  padding: 40px 100px 60px 30px;
  width: 400px;
  margin-right: 100px;
  background-color: rgba(228, 229, 234, 0.7);
  box-shadow: 1px 1px 2px 1px rgba(0, 0, 0, 0.2);
}

.contacts__note {
  margin-bottom: 30px;
}

.contacts__input {
  resize: none;
  display: block;
  width: 100%;
  background-color: rgba(228, 229, 234, 0.7);
  margin-bottom: 20px;
  border: none;
  border-radius: 5px;
  padding: 10px 5px 10px 30px;
  font-size: 16px;
}

.contacts__h4 {
  margin-bottom: 20px;
}

.contacts__info {
  margin-bottom: 10px;
}

.contacts__button {
  width: 100%;
  padding: 10px 5px;
  background-color: #f61729;
  color: #fff;
  transition: background-color 0.5s, color 0.5s;
}

.contacts__button:hover {
  background-color: #fff;
  color: #f61729;
}

.contacts__link {
  color: #000;
}

/*

	END Contacts

	Footer

*/

/* Copyright */

.copyright {
  padding: 10px 10px;
}

.copyright__p {
  color: #777777;
  font-size: 12px;
  line-height: 1rem;
}

/*

	END Footer

	Modal

*/

/* Для модального окна */

section.modal {
  background-color: rgba(0, 0, 0, 0.7);
  position: fixed;
  width: 100%;
  height: 100%;
  top: 0;
  left: 0;
  display: flex;
  justify-content: center;
  align-items: center;
  z-index: 5000;
}

.modal__main {
  position: relative;
  width: 500px;
  padding: 5px;
  max-width: 80%;
  z-index: 5001;
}

i.closer {
  position: absolute;
  top: 2px;
  right: 10px;
  height: 20px;
  color: #333333;
  transition: color .5s;
  z-index: 5002;
}

.modal__content {
  display: none;
  background-color: #fff;
  width: 600px;
  max-width: 100%;
  max-height: 80vh;
}

.modal__content.active {
  display: block;
}

.modal__content.more {
  overflow: auto;
  padding: 20px;
}

.modal__content.more p {
  margin-top: 10px;
  margin-bottom: 20px;
  text-indent: 10px;
}

.modal__content.more p:first-letter {
  font-weight: bold;
  color: #f61729;
}

.modal__content.contact-us {
  padding: 20px 10px 50px;
}

.modal__content.contact-us form {
  padding: 0;
  margin: 0;
  width: 100%;
}

.modal__content.contact-us form p {
  margin-top: 15px;
  color: #000;
  text-align: center;
}

.modal__content.watch-video {
  margin-top: 70px;
  background-color: transparent;
}

.modal__content.watch-video figure {
  width: 100%;
  height: 100%;
  padding: 0;
}

/*

	Functional styles

*/

.button-up {
  position: fixed;
  bottom: 30px;
  right: 30px;
  opacity: 0;
  background-color: #777;
  width: 50px;
  height: 50px;
  border-radius: 10px;
  transition: opacity 0.5s;
  z-index: 4001;
}

.button-up.showed {
  opacity: 0.7;
}

.button-up a {
  display: block;
  width: 20px;
  height: 20px;
  position: absolute;
  z-index: 4000;
  top: 50%;
  left: 50%;
  margin-top: -10px;
  margin-left: -10px;
  border-left: 1px solid #f61729;
  border-top: 1px solid #f61729;
  transform: rotate(45deg);
  text-decoration: none;
}

